    transform: translate(-50%, -50%) perspective(160px) rotateY(1440deg) rotateX(0deg);
  }
  90%, 100% {
    opacity: 1;
    -webkit-transform: translate(-50%, -50%) perspective(160px) rotateY(2880deg) rotateX(90deg);
