.burger-icon-2 {
  position: relative;
  width: 42px;
  height: 42px;
  cursor: pointer;
  -webkit-transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  top: auto;
  right: auto;
  z-index: 999999;
  display: block;
  background-color: var(--bs-brand-secondary);
  border-radius: 4px;
  line-height: 40px;
  text-align: center;
}
.burger-icon-2 img {
  display: inline-block;
  vertical-align: middle;
}

.burger-icon {
  position: absolute;
  width: 42px;
  height: 42px;
  background-color: transparent;
  border-radius: 4px;
  cursor: pointer;
  -webkit-transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  top: 0px;
  right: 0px;
  z-index: 999999;
  display: none;
}
.burger-icon > span {
  display: block;
  position: absolute;
  left: 8px;
  width: 24px;
  height: 1px;
}
.burger-icon > span.burger-icon-top {
  top: 14px;
}
.burger-icon > span.burger-icon-bottom {
  bottom: 14px;
}
.burger-icon > span.burger-icon-mid {
  top: 20px;
}
.burger-icon img {
  display: inline-block;
  vertical-align: middle;
}
.burger-icon.burger-close > span.burger-icon-top {
  display: none;
  opacity: 0;
}
.burger-icon.burger-close > span.burger-icon-mid {
  top: 20px;
  transform: rotate(45deg);
}
.burger-icon.burger-close > span.burger-icon-bottom {
  bottom: 21px;
  transform: rotate(-45deg);
}

.burger-icon > span::before,
.burger-icon > span::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  background-color: #333;
}

.sidebar-visible .burger-icon.burger-icon-white > span::before,
.sidebar-visible .burger-icon.burger-icon-white > span::after {
  background-color: var(--bs-neutral-950);
}

.burger-icon.burger-icon-white > span::before,
.burger-icon.burger-icon-white > span::after {
  background-color: var(--bs-neutral-0);
}

