.btn {
    font-family: $font-manrope;
    border-radius: 0px;
    padding: 14px 23px;
    font-size: $font-lg;
    line-height: 1;
    transition: 0.2s;
    &:hover {
      color: var(--bs-warning);
    }
    &.btn-dropdown {
      padding: 8px 35px 8px 18px;
      border-radius: 50px;
      border: 1px solid var(--bs-neutral-200);
      color: var(--bs-neutral-1000);
      font-size: 14px;
      line-height: 22px;
      font-weight: 500;
      background: var(--bs-neutral-200) url(../imgs/template/icons/arrow-down.svg) no-repeat right 15px center;
      &:after {
        display: none;
      }
      &:hover {
        background-color: var(--bs-neutral-100);
      }
    }
    &.btn-black {
      padding: 8px 18px !important;
      color: var(--bs-neutral-0) !important;
      font-size: 14px !important;
      line-height: 22px !important;
      font-family: $font-manrope;
      font-weight: 700 !important;
      background-color: var(--bs-neutral-1000);
      border-radius: 50px;
      border: 0;
      &:hover {
        color: var(--bs-neutral-1000) !important;
        background-color: var(--bs-neutral-100);
      }
    }
    &.btn-gray {
      padding: 8px 18px !important;
      color: var(--bs-neutral-1000) !important;
      font-size: 14px !important;
      line-height: 22px !important;
      font-family: $font-manrope;
      font-weight: 700 !important;
      background-color: var(--bs-button-bg-2);
      border-radius: 50px;
      border: 1px solid var(--bs-border-color);
      svg {
        stroke: var(--bs-neutral-1000);
        margin-left: 4px;
      }
      &:hover {
        color: var(--bs-neutral-0) !important;
        background-color: var(--bs-neutral-1000);
        border: 0;
        svg {
          stroke: var(--bs-neutral-0);
        }
      }
    }
    &.btn-black-lg {
      padding: 12px 22px !important;
      color: var(--bs-neutral-0) !important;
      font-size: 16px !important;
      line-height: 26px !important;
      font-family: $font-manrope;
      font-weight: 700 !important;
      background-color: var(--bs-neutral-1000);
      border-radius: 50px;
      display: flex;
      align-items: center;
      width: fit-content;
      svg {
        stroke: var(--bs-neutral-0);
        margin-left: 13px;
      }
      svg.first {
        margin-left: 0px;
        margin-right: 8px;
      }
      &:hover {
        color: var(--bs-neutral-1000) !important;
        background-color: var(--bs-neutral-200);
        svg {
          stroke: var(--bs-neutral-1000);
        }
      }
    }
    &.btn-black-lg-square {
      padding: 12px 22px !important;
      color: var(--bs-neutral-0) !important;
      font-size: 16px !important;
      line-height: 26px !important;
      font-family: $font-manrope;
      font-weight: 700 !important;
      background-color: var(--bs-neutral-1000);
      border-radius: 8px;
      display: flex;
      align-items: center;
      width: fit-content;
      svg {
        stroke: var(--bs-neutral-0);
        margin-left: 13px;
      }
      svg.first {
        margin-left: 0px;
        margin-right: 8px;
      }
      &:hover {
        color: var(--bs-neutral-1000) !important;
        background-color: var(--bs-neutral-200);
        svg {
          stroke: var(--bs-neutral-1000);
        }
      }
    }
    &.btn-bg-2-lg-square {
      padding: 12px 22px !important;
      color: var(--bs-neutral-1000) !important;
      font-size: 16px !important;
      line-height: 26px !important;
      font-family: $font-manrope;
      font-weight: 700 !important;
      background-color: var(--bs-button-bg-2);
      border-radius: 8px;
      display: flex;
      align-items: center;
      width: fit-content;
      svg {
        stroke: var(--bs-neutral-1000);
        margin-left: 13px;
      }
      svg.first {
        margin-left: 0px;
        margin-right: 8px;
      }
      &:hover {
        color: var(--bs-neutral-0) !important;
        background-color: var(--bs-neutral-1000);
        svg {
          stroke: var(--bs-neutral-0);
        }
      }
    }
    &.btn-tag {
      padding: 12px 22px;
      font-size: 14px;
      line-height: 22px;
      font-weight: 700;
      background-color: var(--bs-neutral-100);
      border-radius: 50px;
      color: var(--bs-neutral-1000);
    }
    &.btn-tag-sm {
      padding: 9px 16px;
      font-size: 12px;
      line-height: 18px;
      border-radius: 50px;
      color: var(--bs-neutral-1000);
      background-color: var(--bs-neutral-0);
      border: 1px solid var(--bs-border-color);
      &:hover {
        background-color: var(--bs-neutral-1000);
        color: var(--bs-neutral-0);
        box-shadow: $box-shadow-2;
      }
    }
    &.btn-tag-white {
      padding: 10px 22px;
      font-size: 14px;
      line-height: 22px;
      font-weight: 700;
      background-color: var(--bs-neutral-0);
