      color: var(--bs-neutral-1000);
    }
    &.btn-brand-secondary {
      padding: 12px 22px;
      font-size: 16px;
      line-height: 26px;
      font-weight: 700;
      background-color: var(--bs-brand-secondary);
      border-radius: 50px;
      color: var(--bs-color-1000) !important;
      display: flex;
      align-items: center;
      width: fit-content;
      svg {
        stroke: var(--bs-neutral-1000);
        margin-left: 13px;
      }
      .first {
        margin-left: 0px;
        margin-right: 8px;
      }
      &:hover {
        color: var(--bs-neutral-1000) !important;
        background-color: var(--bs-neutral-200);
      }
    }
    &.btn-brand-secondary-square {
      padding: 12px 22px;
      font-size: 16px;
      line-height: 26px;
      font-weight: 700;
      background-color: var(--bs-brand-secondary);
      border-radius: 16px;
      color: var(--bs-color-1000) !important;
      display: flex;
      align-items: center;
      width: fit-content;
      border: 3px solid var(--bs-color-white);
      span {
        display: inline-block;
        width: 27px;
        height: 27px;
        border-radius: 50%;
        background-color: var(--bs-background-black);
        text-align: center;
        line-height: 23px;
        margin-left: 13px;
      }
      svg {
        stroke: var(--bs-color-white);
      }
      .first {
        margin-left: 0px;
        margin-right: 8px;
      }
      &:hover {
        color: var(--bs-neutral-1000) !important;
        background-color: var(--bs-neutral-200);
      }
    }
    &.btn-support-square {
      padding: 21px 22px;
      font-size: 16px;
      line-height: 20px;
      font-weight: 700;
      background-color: var(--bs-background-black);
      border-radius: 16px;
      color: var(--bs-color-white) !important;
      display: flex;
      align-items: center;
      width: fit-content;
      border: 3px solid var(--bs-color-white);
      span {
        display: inline-block;
        width: 27px;
        height: 27px;
        border-radius: 50%;
        background-color: var(--bs-background-white);
        text-align: center;
        line-height: 23px;
        margin-left: 13px;
      }
      svg {
        stroke: var(--bs-color-black);
      }
      .first {
        margin-left: 0px;
        margin-right: 8px;
      }
      &:hover {
        color: var(--bs-neutral-1000) !important;
        background-color: var(--bs-neutral-200);
      }
    }
    &.btn-payment {
      padding: 0px 17px;
      height: 50px;
      line-height: 45px;
      border: 1px solid var(--bs-border-color);
      border-radius: 5px;
      background-color: var(--bs-neutral-0);
      text-align: center;
      margin-right: 10px;
      img {
        display: inline-block;
        vertical-align: middle;
      }
    }
    &.btn-default {
      padding: 11px 18px;
      border-radius: 50px;
      background-color: var(--bs-neutral-1000);
      color: var(--bs-neutral-0);
      font-size: 14px;
      line-height: 22px;
      font-weight: 700;
    }
    &.btn-mode {
      padding: 11px 18px;
      border-radius: 50px;
      background-color: var(--bs-neutral-100);
      color: var(--bs-neutral-1000);
      font-size: 14px;
      line-height: 22px;
      font-weight: 700;
      img {
        display: block;
        vertical-align: middle;
      }
    }
    &.btn-dropdown-search {
      font-size: 14px;
      line-height: 22px;
      font-weight: 700;
      border: 0px;
      color: var(--bs-neutral-1000);
      background-color: transparent;
      position: relative;
      width: auto;
      text-align: left;
      padding: 5px 40px 5px 5px;
      position: relative;
      &:after {
        content: "";
        height: 10px;
        width: 14px;
        background-color: transparent;
        background-image: url(../imgs/page/homepage2/dropdown.svg);
        background-position: center;
        background-repeat: no-repeat;
        position: absolute;
        right: 10px;
        top: 10px;
        border: 0px;
      }
    }
    &.btn-border-1 {
      padding: 16px;
      border: 1px solid var(--bs-border-color);
      border-radius: 8px;
      background-color: var(--bs-neutral-0);
      font-size: 16px;
      line-height: 26px;
      font-weight: 700;
      color: var(--bs-neutral-1000);
      svg, img {
        margin-right: 12px;
        fill: var(--bs-neutral-500);
      }
      img {
        display: inline-block;
        vertical-align: middle;
      }
      &:hover {
        box-shadow: $box-shadow-2;
      }
    }
    &.btn-how-work {
      font-size: 16px;
      line-height: 26px;
      font-weight: 700;
      color: var(--bs-neutral-1000);
      display: flex;
      align-items: center;
      width: fit-content;
      padding: 0px;
    }
    &.btn-white {
      background-color: var(--bs-neutral-0);
      border: 0px solid var(--bs-border-color);
      border-radius: 41px;
      font-size: 26px;
      line-height: 32px;
      font-weight: 700;
      color: var(--bs-neutral-1000);
      padding: 12px 16px;
      &:hover {
        box-shadow: $box-shadow-2;
      }
      img {
        margin-right: 14px;
        display: inline-block;
        vertical-align: middle;
      }
    }
    &.btn-white-sm {
      padding: 7px 21px 9px 21px;
      background-color: var(--bs-neutral-0);
      border: 1px solid var(--bs-border-color);
      border-radius: 38px;
      min-width: 140px;
      font-size: 16px;
      line-height: 18px;
      font-weight: 700;
      color: var(--bs-neutral-1000);
      &:hover {
        box-shadow: $box-shadow-2;
      }
      img {
        display: inline-block;
        vertical-align: middle;
        margin: 0px 1px;
      }
    }
    &.btn-white-md {
      padding: 10px 21px 10px 21px;
      background-color: var(--bs-neutral-0);
      border: 1px solid var(--bs-border-color);
      border-radius: 38px;
      min-width: auto;
      font-size: 16px;
      line-height: 18px;
      font-weight: 700;
      color: var(--bs-neutral-1000);
      &:hover {
        box-shadow: $box-shadow-2;
      }
      img {
        display: inline-block;
        vertical-align: middle;
        margin: 0px 1px;
      }
    }
    &.btn-signin {
      padding: 11px 18px;
      background-color: var(--bs-neutral-0);
      border: 1px solid var(--bs-border-color);;
      border-radius: 38px;
      font-size: 16px;
      line-height: 26px;
      font-weight: 700;
      color: var(--bs-neutral-1000);
      &:hover {
        box-shadow: $box-shadow-2;
      }
    }
    &.btn-label-tag {
      padding: 7px 12px;
      font-size: 12px;
      line-height: 12px;
      font-weight: 700;
      background-color: var(--bs-background-1);
      border-radius: 16px;
      border: 1px solid var(--bs-border-color);
      color: var(--bs-neutral-1000);
      &:hover {
        background-color: var(--bs-neutral-1000) !important;
        color: var(--bs-neutral-0);
      }
    }
    &.btn-label-tag-lg {
      padding: 8px 18px;
      font-size: 14px;
      line-height: 22px;
      font-weight: 700;
      background-color: var(--bs-background-1);
      border-radius: 50px;
      border: 1px solid var(--bs-border-color);
      color: var(--bs-neutral-1000);
      &:hover {
        background-color: var(--bs-neutral-1000) !important;
        color: var(--bs-neutral-0);
      }
    }
    &.btn-link {
      color: var(--bs-neutral-1000);
      font-size: 16px;
      line-height: 26px;
      font-weight: bold;
      text-decoration: none;
      svg {
        stroke: var(--bs-neutral-1000);
        margin-left: 12px;
      }
      &:hover {
        color: var(--bs-brand-secondary);
        svg {
            stroke: var(--bs-brand-secondary);
        }
      }
    }
    &.btn-login {
      background-color: var(--bs-neutral-100);
      border-radius: 8px;
      padding: 14px;
      min-width: 56px;
      color: var(--bs-neutral-1000);
      font-size: 14px;
      line-height: 22px;
      border: 1px solid var(--bs-neutral-200);
      height: 56px;
      img {
        display: block;
        margin: auto;
      }
      &:hover {
        background-color: var(--bs-neutral-900);
        color: var(--bs-neutral-0);
        border-color:var(--bs-neutral-900);
      }
    }
    &.btn-google {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 56px;
      img {
        margin-right: 10px;
        margin-left: 0px;
      }
    }
    &.btn-sort {
      padding: 8px 10px;
      svg {
        stroke: var(--bs-neutral-1000);
      }
      &:hover {
        svg {
          stroke: var(--bs-warning);
        }
      }
    }
    &.btn-share {
      padding: 9px 18px;
      border: 1px solid var(--bs-border-color);
      border-radius: 50px;
      background-color: var(--bs-background-card);
      color: var(--bs-button-text-2);
      font-size: 14px;
      line-height: 22px;
      font-weight: bold;
      svg {
        fill: var(--bs-neutral-1000);
        margin-right: 6px;
      }
    }
    &.btn-wishlish {
      padding: 9px 18px;
      border-radius: 50px;
      background-color: var(--bs-button-bg-2);
      color: var(--bs-button-text-2);
      font-size: 14px;
      line-height: 22px;
      font-weight: bold;
      svg {
        fill: var(--bs-neutral-1000);
        margin-right: 6px;
      }
    }
    &.btn-book {
      border-radius: 8px;
      padding: 15px;
      width: 100%;
      background-color: var(--bs-neutral-1000);
      color: var(--bs-brand-secondary);
      font-size: 16px;
      line-height: 26px;
      font-weight: bold;
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        stroke: var(--bs-brand-secondary);
        margin-left: 10px;
      }
    }
    &.btn-tag-border {
      padding: 7px 14px;
      background-color: var(--bs-background-card);
      border: 1px solid var(--bs-border-color);
      border-radius: 50px;
      font-size: 12px;
      line-height: 18px;
      color: var(--bs-button-text-2);
    }
    &.btn-900 {
      padding: 11px 22px;
      color: var(--bs-color-white);
      font-size: 12px;
      line-height: 18px;
      font-weight: bold;
      border-radius: 50px;
      background-color: var(--bs-neutral-900);
    }
    &.btn-4 {
      border-radius: 55px;
      border: 1px solid var(--bs-warning);
      padding: 9px 15px;
      font-size: 16px;
      line-height: 26px;
      font-weight: bold;
      color: var(--bs-color-black);
      display: flex;
      align-items: center;
      width: fit-content;
      background-color: var(--bs-background-9);
      img {
        display: block;
        margin-right: 13px;
      }
    }
    &.btn-filter {
      background-color: var(--bs-background-white);
      border: 1px solid var(--bs-neutral-200);
      padding: 12px 26px;
      border-radius: 50px;
      font-size: 16px;
      line-height: 26px;
      font-weight: bold;
      color: var(--bs-color-black);
      &:hover, &.active {
        background-color: var(--bs-background-black);
        color: var(--bs-color-white);
      }
    }
}
@-webkit-keyframes anime {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes anime {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
// Button play style 1
.video-play-button {
  position: relative;
  z-index: 10;
  transform: translateY(-50%);
  box-sizing: content-box;
  display: inline-block;
  width: auto;
  height: 44px;
  border-radius: 50%;
  padding: 18px 20px 18px 80px;
}
.video-play-button:before {
  content: "";
  position: absolute;
  z-index: 0;
  left: 0%;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  width: 80px;
  height: 80px;
  background: #ffffff56;
  border-radius: 50%;
  animation: pulse-border 1500ms ease-out infinite;
}
.video-play-button:after {
  content: "";
  position: absolute;
  z-index: 1;
  left: 0%;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  width: 80px;
  height: 80px;
  background: $brand-4;
  border-radius: 50%;
  transition: all 200ms;
}
.video-play-button:hover:after {
  background-color: #f1ad34;
}
.video-play-button:before {
  content: "";
  color: red;
  position: absolute;
  z-index: 0;
  left: 0%;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  width: 80px;
  height: 80px;
  background: #ffffff56;
  border-radius: 50%;
  animation: pulse-border 1500ms ease-out infinite;
}
.video-play-button img {
  position: relative;
  z-index: 3;
  max-width: 100%;
  width: auto;
  height: auto;
}
.video-play-button span {
  display: block;
  position: absolute;
  z-index: 3;
  width: 0;
  height: 0;
  border-left: 27px solid var(--bs-neutral-1000);
  border-top: 19px solid transparent;
  border-bottom: 19px solid transparent;
  left: 50%;
  margin-left: -20px;
  top: 22px;
}
.video-play-button {
	-webkit-animation: scale-up-center 1s cubic-bezier(0.680, -0.550, 0.265, 1.550) both;
	        animation: scale-up-center 1s cubic-bezier(0.680, -0.550, 0.265, 1.550) both;
}
